html{
  background-color: #101118;
  display: inline-block;
}
header{
  background-color: #101118;
}
footer{
  background-color: #101118;
}

.particles {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
  background-color: #101118;
}

.particles canvas {
  display: block;
  vertical-align: top;
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
  opacity: 1;
  -webkit-transition: opacity .8s ease, -webkit-transform 1.4s ease;
  transition: opacity .8s ease, transform 1.4s ease;
  overflow-x: scroll;
}

.app{
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
}

#meDiv{
  padding-top: 20%;
  display: flex;
  flex-direction: column;
  font-family: monospace;
  color: whitesmoke;
  font-size: x-large;
  text-align: center;
}

#meNote{
  font-size: x-small;
}
#emailInfo{
  font-size: xx-large;
}